import React, { useEffect, useState } from "react";
import s from "./ActiveOrdersPage.module.css";
import ActiveOrders from "../../misc/ActiveOrders/ActiveOrders";
import { connect } from "react-redux";
import { getOrdersAction } from "../../store/actions/orderActions";
import ModalWindow from "../../misc/ModalWindow/ModalWindow";

const ActiveOrdersPage = ({ getOrders, orders }) => {
    const [isModalVisible, setModalVisible] = useState(false);
    const showModal = () => {
        document.body.style.overflow = "hidden";
        setModalVisible(true);
    };

    useEffect(() => {
        (async () => {
            await getOrders();
        })();
    }, []);

    return (
        <div className={s.main__container}>
            <ModalWindow
                isVisible={isModalVisible}
                setVisibility={setModalVisible}
            />
            <header className={s.header}>
                <div className={s.header__inner}>
                    <div className={s.header__title}>Aktywne zamówienia</div>
                </div>
            </header>
            <div className={s.container}>
                {orders?.activeOrders?.map((order) => (
                    <div className={s.active__orders__item} key={order._id}>
                        <button onClick={showModal} className={s.order__button}>
                            <ActiveOrders order={order} />
                        </button>
                    </div>
                ))}
                {/*                 
                    <ActiveOrders
                        orderNumber="11"
                        orderType="Paczka"
                        orderWeight="21"
                        orderTime="14:88"
                        orderStartPlace="ul. Rynek 3"
                        orderFinishPlace="ul. Lewakowskiego 12/55"
                        orderPrice="28"
                    /> */}

                {/* <div className={s.active__orders__item}>
                    <ActiveOrders
                        orderNumber="11"
                        orderType="Paczka"
                        orderWeight="21"
                        orderTime="14:88"
                        orderStartPlace="ul. Rynek 3"
                        orderFinishPlace="ul. Lewakowskiego 12/55"
                        orderPrice="28"
                    />
                </div>
                <div className={s.active__orders__item}>
                    <ActiveOrders
                        orderNumber="11"
                        orderType="Paczka"
                        orderWeight="21"
                        orderTime="14:88"
                        orderStartPlace="ul. Rynek 3"
                        orderFinishPlace="ul. Lewakowskiego 12/55"
                        orderPrice="28"
                    />
                </div>
                <div className={s.active__orders__item}>
                    <ActiveOrders
                        orderNumber="11"
                        orderType="Paczka"
                        orderWeight="21"
                        orderTime="14:88"
                        orderStartPlace="ul. Rynek 3"
                        orderFinishPlace="ul. Lewakowskiego 12/55"
                        orderPrice="28"
                    />
                </div> */}
            </div>
        </div>
    );
};
const mapStateToProps = (state) => {
    return { orders: state.order.orders };
};
const mapDispatchToProps = (dispatch) => {
    return {
        getOrders: () => dispatch(getOrdersAction()),
    };
};
export default connect(mapStateToProps, mapDispatchToProps)(ActiveOrdersPage);
